<template>
  <div class="radio">
    <fx-card title="基础用法">
      <fx-radio v-model="baseRadio" label="1">备选项1</fx-radio>
      <fx-radio v-model="baseRadio" label="2">备选项2</fx-radio>
      <p>当前选择: {{ baseRadio }}</p>
      <template #hide>
        <pre v-highlight="base"><code></code></pre>
      </template>
    </fx-card>
    <fx-card title="禁用">
      <fx-radio v-model="disabledRadio" label="1" disabled>备选项1</fx-radio>
      <fx-radio v-model="disabledRadio" label="2" disabled>备选项2</fx-radio>
      <template #hide>
        <pre v-highlight="disabled"><code></code></pre>
      </template>
    </fx-card>
    <fx-card title="带边框">
      <fx-radio v-model="borderRadio" label="1" border>备选项1</fx-radio>
      <fx-radio v-model="borderRadio" label="2" border>备选项2</fx-radio>
      <fx-radio v-model="borderRadio" label="3" border disabled>备选项3</fx-radio>
      <template #hide>
        <pre v-highlight="border"><code></code></pre>
      </template>
    </fx-card>
    <fx-card title="按钮排版">
      <ul class="list">
        <li>
          <fx-radio-group v-model="buttonRadio">
            <fx-radio-button label="1">备选项1</fx-radio-button>
            <fx-radio-button label="2">备选项2</fx-radio-button>
            <fx-radio-button label="3">备选项3</fx-radio-button>
            <fx-radio-button label="4">备选项4</fx-radio-button>
          </fx-radio-group>
        </li>
        <li>
          <fx-radio-group v-model="buttonRadio" disabled>
            <fx-radio-button label="1" disabled>备选项1</fx-radio-button>
            <fx-radio-button label="2">备选项2</fx-radio-button>
            <fx-radio-button label="3">备选项3</fx-radio-button>
            <fx-radio-button label="4">备选项4</fx-radio-button>
          </fx-radio-group>
