            <fx-radio-button label="2">备选项2</fx-radio-button>
            <fx-radio-button label="3">备选项3</fx-radio-button>
            <fx-radio-button label="4">备选项4</fx-radio-button>
          </fx-radio-group>
        </li>
        <li>
          <fx-radio-group v-model="buttonRadio" size="default">
            <fx-radio-button label="1">备选项1</fx-radio-button>
            <fx-radio-button label="2">备选项2</fx-radio-button>
            <fx-radio-button label="3">备选项3</fx-radio-button>
            <fx-radio-button label="4">备选项4</fx-radio-button>
          </fx-radio-group>
        </li>
        <li>
          <fx-radio-group v-model="buttonRadio" size="mini">
            <fx-radio-button label="1">备选项1</fx-radio-button>
            <fx-radio-button label="2">备选项2</fx-radio-button>
            <fx-radio-button label="3">备选项3</fx-radio-button>
            <fx-radio-button label="4">备选项4</fx-radio-button>
