  <fx-radio label="1">备选项1</fx-radio>
  <fx-radio label="2">备选项2</fx-radio>
  <fx-radio label="3">备选项3</fx-radio>
  <fx-radio label="4">备选项4</fx-radio>
</fx-radio-group>`
    const button = `<fx-radio-group v-model="buttonRadio">
  <fx-radio-button label="1" disabled>备选项1</fx-radio-button>
  <fx-radio-button label="2">备选项2</fx-radio-button>
  <fx-radio-button label="3">备选项3</fx-radio-button>
  <fx-radio-button label="4">备选项4</fx-radio-button>
</fx-radio-group>`

    return {
      baseRadio,
      disabledRadio,
      borderRadio,
      sizeRadio,
      groupRadio,
      buttonRadio,
      base,
      disabled,
      border,
      size,
      group,
      button
    }
  }
})
</script>

<style scoped lang="scss">
.radio {
  .list {
    > li {
      padding-bottom: 10px;
    }
  }
}
</style>
