
import { defineComponent, ref } from 'vue'

export default defineComponent({
  name: 'radio',
  setup () {
    const baseRadio = ref<string>('1')
    const disabledRadio = ref<string>('1')
    const borderRadio = ref<string>('1')
    const sizeRadio = ref<string>('1')
    const groupRadio = ref<string>('1')
    const buttonRadio = ref<string>('1')

    const base = `<fx-radio v-model="baseRadio" label="1">备选项1</fx-radio>
<fx-radio v-model="baseRadio" label="2">备选项2</fx-radio>
<p>当前选择: {{ baseRadio }}</p>`
    const disabled = `<fx-radio v-model="disabledRadio" label="1" disabled>备选项1</fx-radio>
<fx-radio v-model="disabledRadio" label="2" disabled>备选项2</fx-radio>`
    const border = `<fx-radio v-model="borderRadio" label="1" border>备选项1</fx-radio>
<fx-radio v-model="borderRadio" label="2" border>备选项2</fx-radio>
<fx-radio v-model="borderRadio" label="3" border disabled>备选项3</fx-radio>`
    const size = `<fx-radio v-model="sizeRadio" label="1" border size="large">备选项1</fx-radio>
<fx-radio v-model="sizeRadio" label="2" border size="middle">备选项2</fx-radio>
<fx-radio v-model="sizeRadio" label="3" border size="default">备选项3</fx-radio>
<fx-radio v-model="sizeRadio" label="4" border size="mini">备选项4</fx-radio>
<fx-radio-group v-model="buttonRadio" size="large">
  <fx-radio-button label="1">备选项1</fx-radio-button>
  <fx-radio-button label="2">备选项2</fx-radio-button>
  <fx-radio-button label="3">备选项3</fx-radio-button>
  <fx-radio-button label="4">备选项4</fx-radio-button>
</fx-radio-group>
<fx-radio-group v-model="buttonRadio" size="middle">
  <fx-radio-button label="1">备选项1</fx-radio-button>
  <fx-radio-button label="2">备选项2</fx-radio-button>
  <fx-radio-button label="3">备选项3</fx-radio-button>
  <fx-radio-button label="4">备选项4</fx-radio-button>
</fx-radio-group>
<fx-radio-group v-model="buttonRadio" size="default">
  <fx-radio-button label="1">备选项1</fx-radio-button>
  <fx-radio-button label="2">备选项2</fx-radio-button>
  <fx-radio-button label="3">备选项3</fx-radio-button>
  <fx-radio-button label="4">备选项4</fx-radio-button>
</fx-radio-group>
<fx-radio-group v-model="buttonRadio" size="mini">
  <fx-radio-button label="1">备选项1</fx-radio-button>
  <fx-radio-button label="2">备选项2</fx-radio-button>
  <fx-radio-button label="3">备选项3</fx-radio-button>
  <fx-radio-button label="4">备选项4</fx-radio-button>
</fx-radio-group>`
    const group = `<fx-radio-group v-model="groupRadio">
  <fx-radio label="1">备选项1</fx-radio>
  <fx-radio label="2">备选项2</fx-radio>
  <fx-radio label="3">备选项3</fx-radio>
  <fx-radio label="4">备选项4</fx-radio>
</fx-radio-group>`
    const button = `<fx-radio-group v-model="buttonRadio">
  <fx-radio-button label="1" disabled>备选项1</fx-radio-button>
  <fx-radio-button label="2">备选项2</fx-radio-button>
  <fx-radio-button label="3">备选项3</fx-radio-button>
  <fx-radio-button label="4">备选项4</fx-radio-button>
</fx-radio-group>`

    return {
      baseRadio,
      disabledRadio,
      borderRadio,
      sizeRadio,
      groupRadio,
      buttonRadio,
      base,
      disabled,
      border,
      size,
      group,
      button
    }
  }
})
